@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-light.woff2") format("woff2"), url("../fonts/roboto-condensed-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumni-sans-semibold.woff2") format("woff2"), url("../fonts/alumni-sans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumni-sans-extrabold.woff2") format("woff2"), url("../fonts/alumni-sans-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  width: 100%;
  background: conic-gradient(from 150.87deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}

.header__container {
  max-width: 1160px;
  margin: 0 auto;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header__container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding: 0 15px;
  }
}

.header__nav {
  padding-top: 30px;
}
@media (max-width: 1199px) {
  .header__nav {
    padding-top: 35px;
    margin-bottom: 18px;
  }
}
@media (max-width: 767px) {
  .header__nav {
    padding-top: 15px;
    margin-bottom: 42px;
  }
}

.header__content {
  display: flex;
  padding-bottom: 51px;
  margin-top: -30px;
}
@media (max-width: 1199px) {
  .header__content {
    margin-top: 0;
    padding-bottom: 82px;
  }
}

.header__description {
  display: grid;
  grid-template-columns: 132px 263px;
  font-family: "Alumni Sans", "Verdana", sans-serif;
  margin-right: 65px;
}
@media (max-width: 1199px) {
  .header__description {
    grid-template-columns: 113px 210px;
    margin-right: 31px;
  }
}
@media (max-width: 767px) {
  .header__description {
    grid-template-columns: 113px 1fr;
    margin: 0;
    width: 100%;
  }
}
.header__description h1 {
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  grid-column: 2/3;
  grid-row: 2/3;
  margin: 0;
  padding-top: 13px;
  -webkit-text-stroke: 4px;
}
@media (max-width: 1199px) {
  .header__description h1 {
    text-transform: none;
    font-size: 72px;
    line-height: 86px;
    padding-top: 9px;
  }
}
@media (max-width: 767px) {
  .header__description h1 {
    padding-top: 14px;
    transform: translateX(-9px);
  }
}
.header__description h2 {
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  text-transform: lowercase;
  grid-column: 2/3;
  margin: 0;
  place-self: end;
  padding-top: 67px;
  padding-right: 50px;
}
@media (max-width: 1199px) {
  .header__description h2 {
    font-size: 34px;
    line-height: 41px;
    padding-top: 20px;
    padding-right: 3px;
  }
}
@media (max-width: 767px) {
  .header__description h2 {
    font-size: 28px;
    line-height: 34px;
    padding-right: 11px;
  }
}

.header__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  grid-column: 1/2;
  grid-row: 2/3;
  place-self: center end;
}
@media (max-width: 1199px) {
  .header__list {
    place-self: end;
  }
}

.header__button {
  grid-column: 2/3;
  place-self: center;
  color: #ffffff;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 4px solid #ffffff;
  padding: 20px 55px;
  transform: translate(18px, -19px);
}
@media (max-width: 1199px) {
  .header__button {
    padding: 20px 32px;
    transform: translate(15px, -7px);
  }
}
@media (max-width: 767px) {
  .header__button {
    padding: 20px 85px;
    grid-column: 1/-1;
    transform: translate(-3px, 22px);
  }
}
.header__button:focus, .header__button:hover {
  background-color: #ffffff;
  color: #102654;
}
.header__button:active {
  opacity: 0.7;
}

@media (max-width: 767px) {
  .header__image {
    display: none;
  }
}

.nav {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
}
.nav a:nth-child(odd) {
  grid-column: 1/2;
  place-self: start;
}
.nav a:nth-child(even) {
  grid-column: 3/4;
  place-self: start end;
}
.nav__logo {
  color: #ffffff;
}
.nav__logo:focus, .nav__logo:hover {
  color: #030a1a;
  opacity: 0.7;
}
.nav__logo:active {
  opacity: 0.9;
}
.nav__phone {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
}
.nav__phone:focus, .nav__phone:hover {
  color: #030a1a;
  opacity: 0.7;
}
.nav__phone:active {
  opacity: 0.9;
}
@media (max-width: 1199px) {
  .nav__phone {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .nav__phone {
    font-size: 20px;
    line-height: 24px;
  }
}

@media (max-width: 1199px) {
  .nav__logo-icon {
    width: 88px;
    height: 17px;
    scale: 0.92;
    transform: translate(-3px, 5px);
  }
}
@media (max-width: 767px) {
  .nav__logo-icon {
    width: 69px;
    height: 24px;
    transform: none;
    scale: 1;
  }
}

.site-list {
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
  padding-top: 57px;
}
@media (max-width: 1199px) {
  .site-list {
    padding-bottom: 50px;
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .site-list {
    padding-bottom: 32px;
    padding-top: 0;
    min-width: 113px;
  }
}

.site-list__link {
  color: #ffffff;
}
.site-list__link:focus, .site-list__link:hover {
  border-bottom: 1px solid #f8f9fc;
  opacity: 0.7;
}
.site-list__link:active {
  opacity: 0.9;
  color: #030a1a;
  border-bottom: 1px solid #030a1a;
}

.hero {
  width: 100%;
}

.hero__container {
  max-width: 1250px;
  padding: 120px 45px;
  margin: 0 auto;
}

.hero__content {
  position: relative;
  display: grid;
  grid-template-columns: max-content 1fr;
}

.hero__description {
  max-width: 355px;
  padding-top: 50px;
  margin-left: 60px;
}
.hero__description h2,
.hero__description h3,
.hero__description p {
  margin: 0;
}
.hero__description h2 {
  font-family: "Alumni Sans", "Verdana", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  margin-bottom: 25px;
}
.hero__description h3 {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 55px;
}
.hero__description p {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 20px;
}

.hero__image {
  place-self: start end;
}

.hero__video {
  position: absolute;
  top: 65%;
  left: 38%;
}

.video__button {
  position: absolute;
  top: 35%;
  left: 40%;
  width: 72px;
  height: 72px;
  border-radius: 50%;
  border: none;
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  outline: none;
  cursor: pointer;
}
.video__button:focus, .video__button:hover {
  scale: 1.1;
}
.video__button:focus .video__icon, .video__button:hover .video__icon {
  color: #b6bdcc;
}
.video__button:active .video__icon {
  color: #f8f9fc;
}
.video__button:active::after {
  content: none;
}
.video__button::after {
  position: absolute;
  top: -20%;
  left: -20%;
  content: "";
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px solid rgba(0, 62, 183, 0.2);
  background: linear-gradient(to right, rgba(237, 2, 51, 0.2), rgba(0, 62, 183, 0.2));
  mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}

.video__icon {
  color: #ffffff;
}

